import { ProductVariantAttribute } from "@/type/product.interface";
import React, { useState } from "react";
import {
  EXAMPLE_CATEGORY_BRAND_JSON,
  EXAMPLE_CATEGORY_VARIANT_JSON,
} from "../data";

interface ProductInformationState {
  name: string;
  slug: string;
  category: string;
  descripiton: string;
  brand?: string;
}

interface ProductVaritantAttributeState {
  name: string;
  value: string;
}

interface ProductVaritantState {
  sku: string;
  attributes: ProductVariantAttribute[];
}

interface ProductPayloadState {
  info: ProductInformationState;
  original_price: number;
  varitants: ProductVaritantState[];
  sale: number;
  images: string[];
  thumbnail: string;
}
export default function CreateNewProduct() {
  /**
   * product informtaion state
   */
  const [information, setInformation] = useState<ProductInformationState>({
    category: "",
    descripiton: "",
    name: "",
    slug: "",
    brand: "",
  });
  /**
   * product varitant state
   */
  const [varitant, setVaritant] = useState<ProductVaritantState[]>([]);
  /**
   * product payload state
   */
  const [payload, setPayload] = useState<ProductPayloadState>({
    images: [],
    info: information,
    original_price: 0,
    sale: 0,
    thumbnail: "",
    varitants: [],
  });
  /**
   * render
   */
  return (
    <div className="w-full text-gray-700">
      {/* information */}
      <div className="flex flex-col gap-3">
        <div className="flex flex-col gap-1">
          <label htmlFor="name" className="font-semibold uppercase text-sm">
            Tên sản phẩm <span className="text-red-500">*</span>
          </label>
          <input
            type="text"
            className="border border-gray-300 outline-0 p-1 rounded-0"
            name="name"
            id="name"
            required
            onChange={(e) => {
              setInformation((prev) => ({ ...prev, name: e.target.value }));
            }}
          />
        </div>
        {/* brand */}
        <div className="flex flex-col gap-1">
          <label
            htmlFor="description"
            className="font-semibold uppercase text-sm"
          >
            Mô tả sản phẩm <span className="text-red-500">*</span>
          </label>
          <textarea
            className="border border-gray-300 outline-0 p-1 rounded-0 h-[300px]"
            name="description"
            id="description"
            required
            onChange={(e) => {
              setInformation((prev) => ({ ...prev, name: e.target.value }));
            }}
          />
        </div>
        {/* original price & sale */}
        <div className="grid grid-cols-2 items-center gap-5">
          {/* original price */}
          <div className="flex flex-col gap-1">
            <label htmlFor="original_price">
              Giá gốc của sản phẩm <span className="text-red-500">*</span>
            </label>
            <input
              type="number"
              name="original_price"
              onWheel={(e) => e.target.blur()}
              required
              onChange={(e) =>
                setPayload((prev) => ({
                  ...prev,
                  original_price: Number(e.target.value),
                }))
              }
              id="original_price"
              className="border border-gray-300 outline-0 p-1"
            />
          </div>
          {/* sale */}
          <div className="flex flex-col gap-1">
            <label htmlFor="sale">Giá khuyến mãi sản phẩm nếu có.</label>
            <input
              type="number"
              name="sale"
              onWheel={(e) => e.target.blur()}
              onChange={(e) => {
                setPayload((prev) => ({
                  ...prev,
                  sale: Number(e.target.value),
                }));
              }}
              id="sale"
              className="border border-gray-300 outline-0 p-1"
            />
          </div>
        </div>
      <div className="grid grid-cols-2 gap-5">
        
      </div>
      </div>
    </div>
  );
}
